import React from 'react';
import './Form.css';
import axios from 'axios'






class CharitySecond extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      charity:'',
      trading:'',
      nzbn:'',
      postal_address:"",
      name:'',
      mobile:'',
      email:'',
      confirm_email:'',
      password:'',
      confirmpassword:'',

    }
  }
    //The following code snippet makes sure the code is correct and the action is initiated when the button is pressed.
              
    handleSubmit(e) {
      e.preventDefault();
    
      axios({
        method: "POST", 
        url:"http://localhost:3040/send", 
        data:  this.state
      }).then((response)=>{
        if (response.data.status === 'success') {
          alert("Login Successful."); 
          this.resetForm()
        } else if(response.data.status === 'fail') {
          alert("Login failed.")
        }
      })
    }

    resetForm(){
      this.setState({   charity:'',
      trading:'',
      name:'',
      mobile:'',
      email:'',
      password:'',
      confirmpassword:'',})
    }

  
    onCharityChange(event) {
      this.setState({charity: event.target.value})
    }
    onTradingChange(event) {
      this.setState({trading: event.target.value})
    }
    onNZBNChange(event) {
      this.setState({nzbn: event.target.value})
    }
    onPostalAddressChange(event) {
      this.setState({postal_address: event.target.value})
    }
    onNameChange(event) {
      this.setState({name: event.target.value})
    }
    onMobileChange(event) {
      this.setState({mobile: event.target.value})
    }
                  
    onEmailChange(event) {
      this.setState({email: event.target.value})
    }
    onConfirmEmailChange(event) {
      this.setState({confirm_email: event.target.value})
    }
    
    onPasswordChange(event) {
      this.setState({password: event.target.value})
    }
    onConfirmPasswordChange(event) {
      this.setState({confirmpassword: event.target.value})
    }

  render() {


    return (
     
      
  < div className="wrapper">    

<div  className="main1">
<p style={{fontSize: "25px", color:"black",  fontFamily:"'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif",
   fontWeight:"bold", marginTop:"10%"}}> CHARITY REGISTRATION</p><br/><br/><br/><br/><br/>

        <form action="mailto:nehachoudhary@gmail.com" className="form" onSubmit={this.handleSubmit.bind(this)} method="POST"  >
      
        
     
<br/><br/><br/>

      <input type="text" required value={this.state.charity} onChange={this.onCharityChange.bind(this)}  placeholder="Legal name of the Charity *" />
      <a href = "https://www.register.charities.govt.nz/CharitiesRegister/Search" style={{color:"blue"}} title="Follow the link">Click here to find your registered name</a><br/><br/>

      <input type="text" required value={this.state.trading} onChange={this.onTradingChange.bind(this)} placeholder="Trading name *" />
      <a href = "https://www.register.charities.govt.nz/CharitiesRegister/Search" style={{color:"blue"}} title="Follow the link">Click here to find your number</a><br/><br/>

      <input type="text" required value={this.state.nzbn} onChange={this.onNZBNChange.bind(this)} placeholder="NZBN *" />
      <a href = "https://www.register.charities.govt.nz/CharitiesRegister/Search" style={{color:"blue"}} title="Follow the link">Click here to find your number</a><br/><br/>

      <input type= "address" required value={this.state.postal_address} onChange={this.onPostalAddressChange.bind(this)}   placeholder="Postal Address *" />

      <input type="text" required value={this.state.name} onChange={this.onNameChange.bind(this)} placeholder="Contact Person *" />
   
      <input type="tel" required value={this.state.mobile} onChange={this.onMobileChange.bind(this)} placeholder="Mobile *"/>

      
      <input type="email" required value={this.state.email} onChange={this.onEmailChange.bind(this)} placeholder="Email *" autoComplete="off"/>

      <input type="email" required value={this.state.confirm_email} onChange={this.onConfirmEmailChange.bind(this)} placeholder="Confirm Email *" autoComplete="off"/>
   
      <input type= "password" required value={this.state.paasword} onChange={this.onPasswordChange.bind(this)} placeholder="Password *" autoComplete="off"/>

      
      <input type= "password" value={this.state.confirmpassword} onChange={this.onConfirmPasswordChange.bind(this)} placeholder="Confirm Password *" autoComplete="off"/><br/><br/>

    <button type="submit" className="btn1" >Submit</button>

    </form>


</div>
<div className="footer" style={{backgroundColor:"white"}}>
                  
                  <p style={{color:"black"}}>Copyright © 2021</p>
                   <p style={{color:"black"}}> DonatePlus - All Rights Reserved.</p>
                </div>           


   </div>            

)}



          

}
export default CharitySecond;

