import React from 'react';
import './Signupform.css';
import axios from 'axios'




class Signupform extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            organisation: "",
            name: '',
            contact: "",
            email: '',
            message: ''
        }
    }
    //The following code snippet makes sure the code is correct and the action is initiated when the button is pressed.

    handleSubmit(e) {
        e.preventDefault();

        axios({
            method: "POST",
            url: "http://localhost:3040/send",
            data: this.state
        }).then((response) => {
            if (response.data.status === 'success') {
                alert("Message Sent.");
                this.resetForm()
            } else if (response.data.status === 'fail') {
                alert("Message failed to send.")
            }
        })
    }

    resetForm() {
        this.setState({ organisation: "", name: "", contact: "", email: "", message: "" })
    }


    render() {

        return (
            <div className="wrapper">
                



                    <div className="main2">
                        <h1 className="heading1" style={{
                            fontSize: "35px", color: "black", fontFamily: "'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif",
                            fontWeight: "bold"
                        }}>CONTACT US</h1><br /><br /><br /><br />

                        <form action="mailto:nehachoudhary@gmail.com" className="signupform" onSubmit={this.handleSubmit.bind(this)} method="POST">
                            <br /><br /><br />
                            <table className="here1">
                                <tbody>
                                    <tr >
                                        <td className="enter1">Organisation Name* </td>
                                        <td ><input type="text" className="enter3" required name="organisation" value={this.state.organisation} onChange={this.onOrganisationChange.bind(this)} /></td>
                                    </tr>
                                    <tr>
                                        <td className="enter1">Contact Name* </td>
                                        <td ><input className="enter3" type="text" required value={this.state.name} onChange={this.onNameChange.bind(this)} /></td>
                                    </tr>
                                    <tr>
                                        <td className="enter1">Mobile Number* </td>
                                        <td ><input type="tel" className="enter3" required value={this.state.contact} onChange={this.onContactChange.bind(this)} /></td>
                                    </tr>
                                    <tr>
                                        <td className="enter1">Email* </td>
                                        <td ><input type="email" className="enter3" required value={this.state.email} onChange={this.onEmailChange.bind(this)} /></td>
                                    </tr>
                                    <tr>
                                        <td className="enter1">Message(Optional) </td>
                                        <td ><input type="textarea" className="enter3" value={this.state.message} onChange={this.onMessageChange.bind(this)} /></td>
                                    </tr>
                                </tbody>


                            </table>

                            <button type="submit" className="herebutton">Submit</button>
                        </form><br /><br /><br /><br /><br /><br /><br />

                    </div>
                    <div className="footer" style={{ backgroundColor: "white" }}>

                        <p style={{ color: "black" }}>Copyright © 2021</p>
                        <p style={{ color: "black" }}> DonatePlus - All Rights Reserved.</p>
                    </div>




                
            </div>


        )
    }

    onOrganisationChange(event) {
        this.setState({ organisation: event.target.value })
    }
    onNameChange(event) {
        this.setState({ name: event.target.value })
    }
    onContactChange(event) {
        this.setState({ contact: event.target.value })
    }
    onEmailChange(event) {
        this.setState({ email: event.target.value })
    }

    onMessageChange(event) {
        this.setState({ message: event.target.value })
    }




}


export default Signupform;

