import React from 'react';
import './AddDonor.css';






class AddDonor extends React.Component {
  constructor(props) {
    super(props);
   
    
this.state={
    input: {},
    errors: {},
                           //isPasswordShown: false                                          /* eye toggling*/
  };
   
  this.handleChange = this.handleChange.bind(this);
  this.handleSubmit = this.handleSubmit.bind(this);
}
   /*
togglePasswordVisibility =() =>{                                 /* eye toggling*/
 /* const {isPasswordShown} =this.state;                                   /* eye toggling*/
 /* this.setState({isPasswordShown: ! isPasswordShown});                           /* eye toggling*/


handleChange(event) {
  let input = this.state.input;
  input[event.target.name] = event.target.value;

  this.setState({
    input
  });
}
   
handleSubmit(event) {
  event.preventDefault();

  if(this.validate()){
      console.log(this.state);

      let input = {};
      input["fname"] = "";
      input["mname"] = "";
      input["lname"] = "";
      input["contact"] = "";
      input["email"] = "";
      input["password"] = "";
      input["confirm_password"] = "";
      this.setState({input:input});

      this.props.history.push('/thank-you')
  }
}

validate(){
    let input = this.state.input;
    let errors = {};
    let isValid = true;

    if (!input["fname"]) {
      isValid = false;
      errors["fname"] = "Please enter your first name.";
    }
   
    
    if (!input["lname"]) {
      isValid = false;
      errors["lname"] = "Please enter your last name.";
    }
    if (!input["contact"]) {
      isValid = false;
      errors["contact"] = "Please enter your contact number.";
    }

    if (!input["email"]) {
      isValid = false;
      errors["email"] = "Please enter your email Address.";
    }

    if (typeof input["email"] !== "undefined") {
        
      var pattern = new RegExp(/^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i);
      if (!pattern.test(input["email"])) {
        isValid = false;
        errors["email"] = "Please enter valid email address.";
      }
    }

    if (!input["password"] ) {

  // const strongRegex = new RegExp('/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,32}$/');         //need to correct this
  // if (!strongRegex.test(input["password"])) {
      isValid = false;
      errors["password"] = "Please enter valid password.";
    
  }
  

    if (!input["confirm_password"]) {
      isValid = false;
      errors["confirm_password"] = "Please enter your confirm password.";
    }

    if (typeof input["password"] !== "undefined" && typeof input["confirm_password"] !== "undefined") {
        
      if (input["password"] !== input["confirm_password"]) {
        isValid = false;
        errors["password"] = "Passwords don't match.";
      }
    } 

    this.setState({
      errors: errors
    });

    return isValid;
}
   
render() {
  const {isPasswordShown} = this.state;             /* eye toggling*/

    return (
      <div className="wrapper">
      
      

<div className="main1">
<p style={{fontSize: "25px", color:"black",  fontFamily:"'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif",
   fontWeight:"bold", marginTop:"6%"}}>DONOR REGISTRATION </p><br/><br/><br/>

        <form action="mailto:nehachoudhary@gmail.com" className="form_input" onSubmit={this.handleSubmit} method="POST" >

        
        <div className="form-group">
            
            <input 
              type="text" 
              name="fname" 
              value={this.state.input.fname}
              onChange={this.handleChange}
              className="form-control" 
              placeholder="First name *" 
              id="fname"/>
  
              <div className="text-danger">{this.state.errors.fname}</div>
          </div>

          <div className="form-group">
            
            <input 
              type="text" 
              name="mname" 
              value={this.state.input.mname}
              onChange={this.handleChange}
              className="form-control" 
              placeholder="Middle name" 
              id="mname"/>
  
              <div className="text-danger">{this.state.errors.mname}</div>
          </div>
          <div className="form-group">
            
            <input 
              type="text" 
              name="lname" 
              value={this.state.input.lname}
              onChange={this.handleChange}
              className="form-control" 
              placeholder="Last name *" 
              id="lname"/>
  
              <div className="text-danger">{this.state.errors.lname}</div>
          </div>

          <div className="form-group">
            
            <input 
              type="text" 
              name="contact" 
              value={this.state.input.contact}
              onChange={this.handleChange}
              className="form-control" 
              placeholder="Mobile number *" 
              id="contact"/>
  
              <div className="text-danger">{this.state.errors.contact}</div>
          </div>
  
          <div className="form-group">
           
            <input 
              type="text" 
              name="email" 
              value={this.state.input.email}
              onChange={this.handleChange}
              className="form-control" 
              placeholder="Email *" 
              id="email" />
  
              <div className="text-danger">{this.state.errors.email}</div>
          </div>
   
          <div className="form-group">
           
            <input 
              type={(isPasswordShown) ? "text" : "password"}                                    /* eye toggling*/
              name="password" 
              value={this.state.input.password}
              onChange={this.handleChange}
              className="form-control" 
              placeholder="Password *" 
              required
              id="password" />
{/*
<i className={`fa ${isPasswordShown ? "fa-eye-slash" : "fa-eye"} password-icon`} onClick={this.togglePasswordVisibility}/>    {/* eye toggling*/}    

  
              <div className="text-danger">{this.state.errors.password}</div>
          </div>
  
          <div className="form-group">
            
            <input 
              type="password" 
              name="confirm_password" 
              value={this.state.input.confirm_password}
              onChange={this.handleChange}
              className="form-control" 
              placeholder="Confirm password *" 
              id="confirm_password" />


              <div className="text-danger">{this.state.errors.confirm_password}</div>

              <input type="submit" value="Submit" className="btn5" />
          </div>

        

   
    </form>


     

</div>
<div className="footer" style={{backgroundColor:"white"}}>
                  
                  <p style={{color:"black"}}>Copyright © 2021</p>
                   <p style={{color:"black"}}> DonatePlus - All Rights Reserved.</p>
                </div>        



                


               

</div>
  )}
  }
export default AddDonor;

