import React from 'react';
import './Form.css';
import axios from 'axios';
import {Link} from "react-router-dom";
import SearchResultDonor  from "./SearchResultDonor";
import moment from "moment";





class CharityaddDonor extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      fname:'',
      mname:'',
      lname:'',
      contact:'',
      email:'',
      physical_address:'',
      postal_address:'',
      password:'',
      confirm_password:'',
      donationDate: moment()
        .locale('en')
        .format('YYYY-MM-DD')
    
    }
  }


  
    //The following code snippet makes sure the code is correct and the action is initiated when the button is pressed.
              
    handleSubmit(e) {
      e.preventDefault();
    
      axios({
        method: "POST", 
        url:"http://localhost:3040/send", 
        data:  this.state
      }).then((response)=>{
        if (response.data.status === 'success') {
          alert("Login Successful."); 
          this.resetForm()
        } else if(response.data.status === 'fail') {
          alert("Login failed.")
        }
      })
    }

    resetForm(){
      this.setState({   fname:'',
      mname:'',
      lname:'',
      email:'',
      contact:'',
      donation:''})
    }

    handleChange = e => {
      this.setState({donationDate: e.target.value})
    }  

 
    onFnameChange(event) {
      this.setState({fname: event.target.value})
    }          
    onMnameChange(event) {
      this.setState({mname: event.target.value})
    }
    onLnameChange(event) {
      this.setState({lname: event.target.value})
    }
    onCnameChange(event) {
       this.setState({cname: event.target.value})
     }
   
    onEmailChange(event) {
      this.setState({email: event.target.value})
    }
    onContactChange(event) {
       this.setState({contact: event.target.value})
     }
    
    onDonationChange(event) {
      this.setState({donation: event.target.value})
    }
    

  


  render() {

    let {donationDate} = this.state
    
    return (
      < div className="wrapper">

  <div className="fulldiv">
  

      
            

            <form action="" className="form" onSubmit={this.handleSubmit.bind(this)} method="POST" >

        
      
            <h1 className="add-donor">ADD DONOR</h1><br/><br/><br/>
      
<input type="text" required   value={this.state.fname} onChange={this.onFnameChange.bind(this)} placeholder="First Name *"/>

<input type="text"   value={this.state.mname} onChange={this.onMnameChange.bind(this)} placeholder="Middle Name "/>

<input type="text" required value={this.state.lname} onChange={this.onLnameChange.bind(this)} placeholder="Last Name *"/>

<input type="text"  value={this.state.cname} onChange={this.onCnameChange.bind(this)} placeholder="Company Name (Optional) "/>

<input type="email" required value={this.state.email} onChange={this.onEmailChange.bind(this)} placeholder="Email *"/>

<input type="tel" required value={this.state.contact} onChange={this.onContactChange.bind(this)} placeholder="Mobile Number *"/>


<input type="text" required value={this.state.donation} onChange={this.onDonationChange.bind(this)} placeholder="Donation *"/>

<br/><br/>
<button type="submit" className="btn1">Submit
</button><br/><br/><br/>


</form>

<br/><br/><br/><br/>

<hr/>


<div className="donationentry">
            <h1>Enter Details of Donation</h1>

           <br/><br/><br/><br/>
           
            <input  type="text" 
        placeholder="Search for donor"
        />
        <SearchResultDonor />

        <input type="text" pattern="[0-9]*" placeholder="Enter donation received"
     />



                <input type="date"
          name="date"
          value={donationDate}
          onChange={this.handleChange}/>

          <br/><br/><br/>

                <h4> Receipt to the donor (form)</h4>

           </div><br/><br/><br/><br/>
            

            
<div className="clickforcontact">

<p style={{fontWeight:"700px", fontSize:"25px"}}>Click <Link to="/contactus" style={{textDecoration:"none", fontWeight:"bold", fontSize:"25px"}}>here </Link>for contact us.</p></div>

<br/><br/><br/><br/>


<div className="footer" style={{backgroundColor:"white"}}>
                  
                  <p style={{color:"black"}}>Copyright © 2021</p>
                   <p style={{color:"black"}}> DonatePlus - All Rights Reserved.</p>
                </div>    
        
</div>

</ div>


 )}


 
 
           
 
 }

export default CharityaddDonor;
