import React, {useState} from 'react';
import {CardElement, useElements, useStripe} from "@stripe/react-stripe-js";
import axios from "axios";
import "./FormPayment.css";



function FormPayment() {

    const [success, setSuccess] = useState(false);
    const stripe = useStripe ()
    const elements = useElements ()

    const handleSubmit =async( e) =>{
        e.preventDefault()
        const {error, paymenMethod} = await stripe.createPaymentMethod({
            type:"card",
            card:elements.getElement(CardElement)

        })
    

    if (!error) {
        try{
            const {id} = paymenMethod
            const response = await axios.post("http://localhost:3040/payment", {
                amount: 100,
                id:id
            })
   if (response.data.success){
                console.log ("Successful Payment");
                setSuccess(true);
   }
        }catch(error){
            console.log("Error", error)

        }
    }else{
        console.log(error.message)
    }
}
    return (
        <>
                {!success ?
                <form onSubmit={handleSubmit} className="finalpaymentform">

                            <fieldset className="formGroup">
                            <div className="formRow">

                            
                            <input type="text" placeholder="Name on card" className="cardholder"/>
                            <CardElement

                          /><br/><br/>
                                      <button className="btn4" style={{fontSize:"20px"}}>Pay</button>
                            </div>
    

                            </fieldset>
                          
                </form>
                : 

                <div>

                    <h2>You are done with payment</h2>
                </div>
                
                }

             
        </>
    )
}

export default FormPayment
