import React from 'react';
import './Form.css';
import {   Link } from "react-router-dom";
import axios from 'axios'

class LoginDonor extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      email:'',
      password:''
    
    }
  }
    //The following code snippet makes sure the code is correct and the action is initiated when the button is pressed.
              
    handleSubmit(e) {
      e.preventDefault();
    
      axios({
        method: "POST", 
        url:"http://localhost:3040/send", 
        data:  this.state
      }).then((response)=>{
        if (response.data.status === 'success') {
          alert("Login Successful."); 
          this.resetForm()
        } else if(response.data.status === 'fail') {
          alert("Login failed.")
        }
      })
    }

    resetForm(){
      this.setState({  email:'',
      password:''})
    }

  


  render() {
  



    return (
       <div className="wrapper">




        <div className="main4">

        <p style={{fontSize: "35px", color:"black",  fontFamily:"'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif",
   fontWeight:"bold", marginTop:"10%"}}>DONOR LOGIN</p><br/><br/><br/><br/><br/>
        <form action="mailto:nehachoudhary@gmail.com" className="form" onSubmit={this.handleSubmit.bind(this)} method="POST"  >
      




<input type="text" required  value={this.state.email} onChange={this.onEmailChange.bind(this)}  placeholder="Email *"/>


<input type="text" required value={this.state.password} onChange={this.onPasswordChange.bind(this)}  placeholder="Password *"/>

<Link to="/payment-to-chaity" style={{textDecoration:"none"}}><button type="submit" className="btn1" >Log In</button></Link><br/>


<Link to="/donor-signup" style={{textDecoration:"none"}}><span style={{fontSize:"17px", color:"blue", textDecoration:"none"}}>Forgotten Password?</span></Link>
      <br/>

      <Link to="/donor-signup" style={{textDecoration:"none"}}><button className="btn2" >Create New Account</button></Link> <br/><br/><br/>

</form>     
        </div>

        <div className="footer" style={{backgroundColor:"white"}}>
                  
                  <p style={{color:"black"}}>Copyright © 2021</p>
                   <p style={{color:"black"}}> DonatePlus - All Rights Reserved.</p>
                </div>          
     </div>   
 )}

              
 onEmailChange(event) {
   this.setState({email: event.target.value})
 }
 
 onPasswordChange(event) {
   this.setState({password: event.target.value})
 }

 
 
           
 
 }
export default LoginDonor;
